<template>
	<div style="width: 100%; height:100%;position:absolute;">
		<div id="mapContainer" style="width: 100%; height:100%"></div>
	</div>
</template>


<script>
	import AMapLoader from '@amap/amap-jsapi-loader'
	import {wgs84togcj02} from '../../utils/wgs842gcj02.js'
	var map;
	export default {
		data() {
			return {
				map: null
			};
		},
		mounted() {
			this.initMap();
		},
		methods: {
			initMap() {
				AMapLoader.load({
					key: '	60cadebb9484029330544d1d414a4e9a', // 你的高德地图Key
					version: '2.0', // SDK版本
					plugins: ['AMap.Geolocation'] // 需要使用的插件
				}).then((AMap) => {
					var bbox = document.getElementById('mapContainer')
					map = new AMap.Map(bbox, {
						zoom: 13, // 地图缩放级别
						center: [116.397428, 39.90923] // 地图中心点
					});
					this.map = map

					//异步加载控件
					AMap.plugin('AMap.ToolBar', function() {
						var toolbar = new AMap.ToolBar({
							position: 'LT'
						}); //缩放工具条实例化
						map.addControl(toolbar); //添加控件
					});

					// var geolocation = new AMap.Geolocation({
					// 	position: 'RT',
					// 	convert: true,
					// 	enableHighAccuracy: true, // 是否使用高精度定位，默认：true
					// 	timeout: 2000, // 设置定位超时时间，默认：无穷大
					// 	zoomToAccuracy: true, // 定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
					// 	showButton: true

					// })
					// map.addControl(geolocation)
					// geolocation.getCurrentPosition((status, result) => {
					// 	console.log(status, result)
					// })
					uni.getLocation({
						type: 'wgs84',
						success: function (res) {
							console.log('当前位置的经度：' + res.longitude);
							console.log('当前位置的纬度：' + res.latitude);
							var lnglat = wgs84togcj02(res.longitude,res.latitude)
							console.log(lnglat)
							map.setCenter(lnglat,true)
							const marker = new AMap.Marker({
							  position: lnglat,
							});
							 map.add(marker);
						}
					});
				})

			}
		}
	};

</script>